<template>
  <div>
      <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="">
                     <el-select v-model="form.nycd" placeholder="">
                        <el-option label="" value="4"></el-option>
                        <el-option label="" value="3"></el-option>
                        <el-option label="" value="2"></el-option>
                        <el-option label="" value="1"></el-option>
                    </el-select>
                  </el-form-item>   
                  <el-form-item label="">
                     <el-select v-model="form.sfjj" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                  </el-form-item>   
                  <el-form-item label="">
                      <el-input v-model="gs" placeholder=" :2.5" style="width:174px" readonly></el-input>
                      <span style="color:#A8A8A8;font-size:14px;margin-left:25px">:,</span>
                  </el-form-item>   
                   <el-form-item label="bug">
                      <el-switch v-model="form.sfbg"></el-switch>
                  </el-form-item>  
                   <el-form-item label="">
                        <el-input type="textarea" v-model="form.gbsm" resize="none" rows="4"></el-input>
                    </el-form-item>
                     <el-form-item style="text-align:right">
                        <el-button type="primary" @click="submitForm"></el-button>
                        <el-button type="info" @click="closeForm"></el-button>
                    </el-form-item>
        </el-form> 
  </div>
</template>
<script>
export default {
  data(){
      return {
            form:{
             nycd:'4',
             sfjj:'0',
             gs:0,
             sfbg:false,
             gbsm:""
          },
      }
  },
  props:{
      gs:{
        type:Number,
        default:0
      }  
  },
  methods:{
    submitForm(){
         let reg = /^\d+(\.\d+)?$/;
            if(!reg.test(this.form.gs)){
                this.$alert('', '', {
                confirmButtonText: '',
                type:'warning'
            });
            return;
        }
        this.form.gs = this.gs
       this.$emit('submitForm',this.form);
    },
    closeForm(){
       this.$emit('closeForm','');
    },
  }
}
</script>
<style scoped>

</style>



