    queryCpData(curPage) {
      // 
      queryZjCpData({
        curPage: curPage,
        pageSize: 10,
        rylb: this.rylb,
        keyword: this.cpKeyword
      }).then(({ data }) => {
        if (data.state == "success") {
          this.tableData = data.data.rows;
          this.jkrTotal = data.data.records;
        }
      });
    },
    queryCbRy() {
      //
      queryCbRy({
        wid: this.wid,
        keyword: this.cpKeyword
      }).then(({ data }) => {
        if (data.state == "success") {
          this.tableData = data.data;
          this.tableData.forEach((ele, i, arr) => {
            if (ele.username.slice(0, 1) == 1) {
              ele.userbh = `${ele.username.slice(0, 3)}****${ele.username.slice(
                -4
              )}`;
            } else {
              ele.userbh = ele.username;
            }
          });
        }
      });
    },
    saveCb(ryData) {
      //
      saveCb({
        wid: this.wid,
        nr:
          $("#summernoteT").summernote("code") == "<p><br></p>"
            ? ""
            : $("#summernoteT").summernote("code"),
        ryData: ryData
      }).then(({ data }) => {
        if (data.state == "success") {
          $("#summernoteT").summernote("code", "");
          this.$alert("", "", {
            confirmButtonText: "",
            type: "success",
            callback: action => {
              this.queryAnswers(this.wid);
            }
          });
          if (ryData) {
            this.zdfzrVisible = false;
          }
        }
      });
    },

    forwardingProblem() {
      //
      this.innerZFWTisible = true;
      forwardLabel({
        wid: this.wid
      }).then(({ data }) => {
        if (data.state == "success") {
          if (data.data.length != 0 && data.data != null) {
            this.zfDx = data.data;
            this.radio = data.data[0].bh;
          }
        }
      });
      this.$nextTick(() => {
        $("#summernoteZF").summernote({
          dialogsInBody: true,
          placeholder: "",
          height: 200,
          width: 100 + "%",
          minHeight: 150,
          maxHeight: 400,
          lang: "zh-CN",
          focus: true,
          toolbar: [
            ["style", ["bold", "italic", "underline", "clear"]],
            ["font", ["strikethrough", "superscript", "subscript"]],
            ["fontsize", ["fontsize"]],
            ["color", ["color"]],
            ["para", ["ul", "ol", "paragraph"]],
            ["height", ["height"]],
            ["picture"],
            ["link", ["linkDialogShow", "unlink"]]
          ]
        });
      });
    },

    CommitZFWT() {
      //()
      if (this.radio == 11 && this.zfcp == "") {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return;
      }
      if (!this.qwjjrqZf) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return;
      }
      saveForward({
        wid: this.wid,
        bh: this.radio,
        nr:
          $("#summernoteZF").summernote("code") == "<p><br></p>"
            ? ""
            : $("#summernoteZF").summernote("code"),
        cpxbh: this.radio == 11 ? this.zfcp : "",
        qwjjrq: this.qwjjrqZf
      }).then(({ data }) => {
        if (data.state == "success") {
          this.innerZFWTisible = false;
          this.queryAnswers(this.wid);
          this.queryBtnAuth(this.wid);
          this.queryProcess(this.wid);
          this.queryQuestion(this.wid);
          this.$alert("", "", {
            confirmButtonText: "",
            type: "success"
          });
        }
      });
    },
    CancelZFWT() {
      // 
      this.innerZFWTisible = false;
    },

    remindsQuestion() {
      //
      this.zdTitle = "";
      showCbCondition({
        //
        wid: this.wid
      }).then(({ data }) => {
        if (data.state == "success") {
          if (data.data == 1) {
            this.queryCbRy();
            this.zdfzrVisible = true;
          } else if (data.data == 0) {
            this.saveCb("");
          }
        }
      });
    },

    replyQuestion(param) {
      //
      let reg = /^\d+(\.\d+)?$/;
      let hfnr = $("#summernoteT").summernote("code");
      if (!reg.test(this.gsValue)) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "error"
        });
        return;
      }

      if (hfnr == "<p><br></p>" || hfnr == "") {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
      } else {
        this.isHF = true;
        saveAnswer({
          //
          zbwid: this.wid,
          nr: hfnr,
          gs: this.gsValue,
          sfbg: this.sfbgValue,
          cnjsrq: this.cnjsrq || "",
          hflx: param == "hf" ? 1 : 99,
          tktwr: this.tktwr
        }).then(({ data }) => {
          if (data.state == "success") {
            this.isHF = false;
            this.gsValue = 0;
            this.$alert("", "", {
              confirmButtonText: "",
              type: "success",
              callback: action => {
                this.queryAnswers(this.wid);
                this.queryProcess(this.wid);
                $("#summernoteT").summernote("code", "");
              }
            });
          } else {
            this.isHF = false;
            this.$alert(data.msg, "", {
              confirmButtonText: "",
              type: "error"
            });
          }
        });
      }
    },

    closeQuestion() {
      //
      if (this.qusetionInfo.zlpf != "") {
        this.fwzlValue = JSON.parse(this.qusetionInfo.zlpf);
      }
      queryrReferenceHour({
        wid: this.wid
      }).then(({ data }) => {
        if (data.state == "success") {
          this.ContributionPeople = data.data;
          this.ContributionPeople.forEach((ele, i, arr) => {
            ele.qrgs = ele.confirmH.replace("/^.(d+)/", "0.");
          });
        }
      });

      this.innerVisible = true;
    },

    commitQuestion() {
      //
      let gxrArr = [];
      if (this.ContributionPeople.length != 0) {
        this.ContributionPeople.forEach((ele, i, arr) => {
          gxrArr.push(ele.fbrxm + "," + ele.fbrbh + "," + ele.qrgs);
        });
      }
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          if (this.fwzlValue == 0) {
            this.$alert("", "", {
              confirmButtonText: "",
              type: "warning",
              callback: action => {}
            });
            return;
          }
          closeQuestion({
            wid: this.wid,
            zlpf: this.fwzlValue,
            gxrData: gxrArr.join("|"),
            sfjj: "",
            jjsm:
              $("#summernoteT").summernote("code") == "<p><br></p>"
                ? ""
                : $("#summernoteT").summernote("code")
          }).then(({ data }) => {
            if (data.state == "success") {
              this.innerVisible = false;
              this.$alert(data.msg, "", {
                confirmButtonText: "",
                type: "success",
                callback: action => {
                  this.queryQuestion(this.wid);
                  this.queryAnswers(this.wid);
                  this.queryBtnAuth(this.wid);
                  this.queryProcess(this.wid);
                }
              });
            } else {
              this.$alert(data.msg, "", {
                confirmButtonText: "",
                type: "error"
              });
            }
          });
        })
        .catch(() => {});
    },
    queryProcess(wid) {
      queryProcess({
        //
        wid: wid,
        isTs: ""
      }).then(({ data }) => {
        if (data.state == "success") {
          this.questionProcess = data.data;
          this.questionProcess.forEach((ele, i, arr) => {
            if (ele.flag == 1) {
              this.questionProcess[i].status = "process";
            } else if (ele.flag == 2) {
              this.questionProcess[i].status = "success";
            }
          });
          this.lchjList = this.questionProcess.slice(
            0,
            this.questionProcess.length - 2
          );
        }
      });
    },

    queryQuestion(wid, xmtd) {
      //   
      this.qusetionInfo = {};
      this.wtlbmc = "";
      this.hjgs = 0;
      queryQuestion({
        wid: wid
      }).then(({ data }) => {
        if (data.state == "success") {
          this.qusetionInfo = data.data;
          this.cnjsrq = data.data.qwjjrq;
          document.title = "-" + data.data.bt;
          if (this.qusetionInfo.fbzt == 1) {
            $("#summernoteT").summernote("destroy");
          }
          if (xmtd == 1) {
            isgcXmtdbyWt({
              wid: this.qusetionInfo.wid
            }).then(({ data }) => {
              if (data.state == "success") {
                this.isgcXmtdbyWt = data.data;
              }
            });
          }
          if (this.qusetionInfo.wtlb != "") {
            getResponsibleTaskList({
              name: "ProblemType",
              code: this.qusetionInfo.wtlb
            }).then(({ data }) => {
              if (data.state == "success") {
                this.wtlbmc = data.data;
              }
            });
          }
          //    
          if (data.data.fbzt == 1) {
            this.zlpf = JSON.parse(data.data.zlpf);
            queryContributionPeople({
              //
              wid: this.wid
            }).then(({ data }) => {
              if (data.state == "success") {
                if (data.data != null && data.data.length != 0) {
                  this.yxGXR = data.data;
                  this.yxGXR.forEach((ele, i, arr) => {
                    if (ele.gs.split(".")[0] == "") {
                      ele.gs = 0 + ele.gs;
                    }
                    this.hjgs += parseFloat(ele.gs);
                  });
                } else {
                  this.yxGXR = [];
                }
              }
            });
          }
          try {
            this.qusetionInfo.nr = decodeURIComponent(
              this.qusetionInfo.nr
            ).replace(
              /gcgl\/sys\/emapcomponent\/file\/getFileByToken/g,
              "emap/sys/emapcomponent/file/getFileByToken"
            );
          } catch (err) {
            this.qusetionInfo.nr = this.qusetionInfo.nr;
          }
        }
      });
    },
    // 
    queryAnswers(wid) {
      this.questionReply = [];
      queryAnswers({
        wid: wid,
        isSolution: this.isCjwt ? true : ""
      }).then(({ data }) => {
        if (data.state == "success") {
          this.questionReply = data.data;
          if (data.data != null && data.data.length != 0) {
            this.questionReply.forEach((ele, i, arr) => {
              try {
                ele.nr = decodeURIComponent(ele.nr).replace(
                  "/gcgl/sys/emapcomponent/file/getFileByToken/",
                  "/emap/sys/emapcomponent/file/getFileByToken/"
                );
              } catch (err) {
                ele.nr = ele.nr;
              }
            });
          }
        }
      });
    },
    //  
    queryBtnAuth(wid) {
      queryBtnAuth({
        wid: wid
      }).then(({ data }) => {
        if (data.state == "success") {
          this.btnShwon = data.data;
          this.xgqwjjrq = data.data.qwjjrq;
        }
      });
    },
    queryLabel(wid) {
      //
      queryLabel({
        wid: wid
      }).then(({ data }) => {
        if (data.state == "success") {
          this.wtbq = data.data;
          this.wtbq.forEach((ele, i, arr) => {
            if (ele.sjly == 0) {
              ele.type = "info";
            } else {
              ele.type = "";
            }
          });
        }
      });
    }
  },
  watch: {},
  activated() {},
  components: {
    pagination,
    itemChoose,
    zdfzrChoose,
    sqjsForm,
    twDialog,
    xxDialog
  }
};
</script>
<style scoped>
.project-question {
  width: 95%;
  margin: 0 auto;
  padding: 10px;
}

.question-type {
  text-align: center;
  width: 100px;
}
.questionOperate {
  font-size: 12px;
  white-space: nowrap;
  opacity: 0;
}
.questionOperate > span:hover {
  text-decoration: underline #888;
}

.question-type span.el-icon-question {
  font-size: 32px;
  color: #f56c6c;
}
.question-type span.el-icon-success {
  font-size: 30px;
  color: #67c23a;
}

.Question {
  padding: 10px 0;
}
div.el-form-item {
  margin-bottom: 8px !important;
}

/*   detail*/
.project-question-detail {
  padding: 5px 10px;
  background: #fff;
}
.project-question-detail li {
  border-bottom: 1px solid #f1f1f1;
}
.project-question-detail-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #f1f1f1;
}
.project-question-detail .question-reply {
  display: inline-block;
  width: 38px;
  height: 26px;
  border-radius: 3px;
  text-align: center;
  line-height: 24px;
  background: #3ba7f5;
  color: #fff;
  font-size: 14px;
}
.project-question-detail-top .question-content {
  width: 90%;
}
.project-question-detail-top .question-content-SC {
  width: 92%;
  color: #888;
}
.question-content-SC .el-button,
.question-content-SC .el-tag {
  height: 28px;
}
.question-content-SC .el-tag {
  line-height: 28px;
}
.question-content .question-info-front {
  color: #a8a8a8;
  font-size: 12px;
}
.question-content > p {
  margin: 0 0 3px 0 !important;
  width: 100%;
  white-space: nowrap;
  font-size: 14px;
}
.question-content > p > span {
  display: inline-block;
  min-width: 25%;
  white-space: nowrap;
  font-size: 14px;
  color: #363748;
}
.question-content > p > span:nth-of-type(2) {
  width: 40%;
}

.project-question-detail-bottom > div {
  width: 100%;
  overflow: auto;
  padding: 10px 5px 10px 80px;
}
.isshown-query:hover {
  cursor: pointer;
}

.bg-active {
  background: #1997d7;
  color: #fff !important;
}
.question-state {
  text-align: center;
  width: 13%;
}
.question-state > span:nth-of-type(1) {
  padding: 3px 8px;
  border-radius: 2px;
  font-size: 14px;
  display: inline-block;
}
.ygb-color {
  color: #7ece64 !important;
  background: rgba(126, 206, 100, 0.2);
}
.clz-color {
  color: #f56c6c;
  background: rgba(245, 108, 108, 0.2);
}

.question-evaluate {
  padding: 10px;
}
.question-evaluate > p {
  margin-top: 10px !important;
  color: #888;
  font-size: 12px;
}
/* .question-evaluate .ContributionPeople-table {
  border: 1px solid #ccc;
  border-collapse: collapse;
}
.question-evaluate .ContributionPeople-table th {
  border: 1px solid #ccc;
  text-align: center;
}
.question-evaluate .ContributionPeople-table td {
  border: 1px solid #ccc;
  text-align: center;
  width: 33%;
} */
.question-rate {
  margin: 10px 0;
}
.question-rate:after {
  content: "";
  display: block;
  clear: both;
}
.question-rate > p {
  display: flex;
  align-items: center;
  float: left;
}
.question-steps {
  padding: 5px 10px;
  background: #f4f6f9;
}
.question-textarea {
  width: 700px;
  margin: 0 auto;
}
.question-textarea > p {
  width: 125px;
  text-align: right;
  padding-right: 12px;
  font-weight: 700;
}
.question-textarea > p::before {
  content: "*";
  color: #f56c6c;
  margin-right: 4px;
}
.CrowdRwxx {
  margin-bottom: 10px;
}
.CrowdRwxx span:nth-of-type(1),
.CrowdRwxx span:nth-of-type(3) {
  font-weight: 700;
}
.Crowdrwmc {
  display: inline-block;
  width: 350px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: bottom;
}
/* */
.question-sqdyz-title:before {
  content: "*";
  color: #f00;
  padding: 0 2px;
}
.item-wtbq {
  float: left;
  padding: 2px 0;
}
.item-wtbq-tag {
  display: inline-block;
  width: 95%;
  white-space: pre-wrap;
}
.is-outline {
  outline: 1px solid #409eff !important;
}
.filter-bt {
  display: inline-block;
  width: 100px;
  vertical-align: top;
  text-align: right;
  font-weight: 700;
}
</style>



